@import url(https://fonts.googleapis.com/css2?family=Cuprum&display=swap);
body {
  margin: 0;
    width: 100%;
    background-blend-mode: lighten;
    background-color: #cfd7c7;
    background-size: cover;
    background-position: center;
    background-clip: padding-box;
    font-family: 'Cuprum', sans-serif;
    /* scroll-behavior: smooth; */
    margin: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* HEADER */
header {
    top: 0;
}

/* COVER */
header {
    background-color: #cfd7c7; 
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    background-clip: padding-box;
}

header h1, h2 {
    text-align: center;
    color: #0b2027;
}

.header-headline {
    display: flex;
    align-content: center;
    justify-content: center;
    flex-direction: column;
    text-align: center;
    margin-top: 50px;
}

.header-cover {
    display: flex;
    justify-content: center;
    height: 30vh;
    margin-top: 0px;
}

.header-cover img {
    width: 175px;
    height: 170px;
    margin: auto 0;
    padding: 0px;
    border-radius: 300px;
    border-color: #0b2027;
    border-width: 3px;
    border-style: solid;
}

@media only screen and (min-device-width: 320px) and (max-device-width: 600px) {
    .header-cover {
       height: 20vh; 
    }
    
    .header-cover img {
        width: auto;
        height: 5rem;
    }
}

#hr-style {
    width: 20vw;
    margin-bottom: 25px;
    border: 0;
    height: 0; /* Firefox... */
    box-shadow: 0 0 10px 1px black;
}

.buffer {
    height: 35px;
}

.header-nav {
    background-color: #0b2027;
}

/* MAIN NAV */
.sticky {
    position: fixed;
    top: 0;
    width: 100%;
}

.main-nav {
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin: 0;
    list-style: none;
    font-size: 1em;
    padding: 5px;
    cursor: pointer;
}

@media only screen and (min-device-width: 320px) and (max-width: 600px) {
    .main-nav {
        font-size: 0.7em;
        padding: 1px;
    }
}

.main-nav li {
    padding: 20px;
}

.main-nav li > li {
    margin-left: 0px;
}

.main-nav a {
    color: #cfd7c7;
    text-decoration: none;
    text-transform: uppercase;
    letter-spacing: 1px;
}

.main-nav a:hover {
    box-shadow: 0px 10px 5px 0px black;
}

.active-link {
    border-bottom: 3px solid #a44a3f;
}

.main-nav .active {
    color: #a44a3f;
}

.logo {
    margin-right: 100px;
}
/* ABOUT */
.about {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
    grid-gap: 10px;
    gap: 10px;
    margin-top: 20px;
    color: #0b2027;
}

.about h1 {
    font-size: 50px;
    text-align: center;
    grid-row: 1fr;
    padding: 10px;
}

.about p {
    text-align: start;
    line-height: 1.5em;
    padding: 5px;
    font-size: 20px;
    font-style: normal;
    grid-row: span 1;
}

.block-text {
    margin-top: 0;
    margin-left: auto;
    margin-right: auto;
    width: 22em;
}

@media only screen and (min-width: 320px) and (max-width: 350px) {
    .about {
        width: auto;
        padding: 10px;
        display: grid;
        grid-template-rows: 1fr;
        grid-gap: 10px;
        gap: 10px;
        margin-top: 20px;
    }

    .block-text {
        margin-left: auto;
        margin-right: auto;
        width: 12em;
    }
}

@media only screen and (min-width: 350px) and (max-width: 480px) {
    .about {
        width: auto;
        padding: 10px;
        display: grid;
        grid-template-rows: 1fr;
        grid-gap: 10px;
        gap: 10px;
        margin-top: 20px;
    }

    .block-text {
        margin-left: auto;
        margin-right: auto;
        width: 15em;
    }
}

.projects {
    display: grid;
    grid-template-columns: 1fr;
    grid-gap: 20px;
    gap: 20px;
    justify-content: center;
}

.projects h1 {
    font-size: 50px;
    text-align: center;
    grid-row: 1fr;
    padding: 10px;
}

.box {
    padding: 10px;
    margin: 5px auto;
    background-color: #0b2027;
    color: #cfd7c7;
    display: flex;
    flex-direction: column;
    border: 1px solid #a44a3f;
    border-radius:4px;
    border-width: 1px;
}

.box:hover {
    border-color: #a44a3f;
    border-width: 3px;
}

.box h3 {
    text-align: center;
}

.icons a {
    padding: 7px;
    color: #0b2027;
    border-style: solid;
    border-radius: 200px;
    border-width: 2px;
    border-color: #a44a3f;
    background-color: #a44a3f;
}

.block-text {
    min-width: 20rem;
    max-width:  25rem;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    line-height: 1.5em;
    text-align: start;
}

@media only screen and (min-width: 370px) and (max-width: 480px) {
    .box {
        width: auto;
    }

    .block-text {
        font-size: 1em;
        width: 12rem;
    }
}

@media only screen and (max-width: 370px) {
    .box {
        width: 80vw;
    }

    .block-text {
        font-size: 0.8em;
        min-width: 16rem;
    }
}

.center-horizontal {
    display: flex;
    justify-content: center;
}

.icons {
    display: flex;
    list-style: none;
    justify-content: space-around;
    padding: 5px;
    margin-top: auto;
    margin-bottom: 0;
}

footer {
    height: auto;
    background-color: whitesmoke;
    background-size: cover;
    margin-top: 50px;
    padding: 50px
}

footer > p {
    text-align: center;
}

footer a {
    color: inherit;
    text-decoration: underline;
}

/* SIDE NAV */
.body-nav {
    display: flex;
    justify-content: center;
}

.side-nav {
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    width: auto;
    min-width: 20em;
    display: flex;
    list-style: none;
    margin: 5px;
    padding: 10px;
    justify-content: space-around;
    background-color: inherit;
    /* background-image: -o-linear-gradient(#0a66c2, rgb(163, 180, 235));
    background-image: -moz-linear-gradient(#0a66c2, rgb(163, 180, 235));
    background-image: -webkit-linear-gradient(#0a66c2, rgba(163, 180, 235, 0.664)); */
    /* box-shadow: 0 0 10px 1px black;
    border-radius: 10px; */
    margin-top: 20px;
}

.side-nav li {
    font-size: 1.2em;
    padding: 10px;
}

.side-nav li:hover {
    transform: scale(1.1);
}

.side-nav a {
    padding: 7px;
    color: #0b2027;
    border-style: solid;
    border-radius: 200px;
    border-width: 2px;
    border-color: #a44a3f;
    background-color: #a44a3f;
}

@media only screen and (min-width: 320px) and (max-width: 600px) {
    .side-nav {
        width: auto;
        min-width: -webkit-fit-content;
        min-width: -moz-fit-content;
        min-width: fit-content;
        height: auto;
        padding: 5px;
    }

    .side-nav > li {
        font-size: 1em;
        margin: 5px;
        padding: 5px;
    }
}
